import React from 'react';
import { Link } from 'react-router-dom';
import { Home, FileText, Shield, Sun } from 'react-feather';
import Tooltip from '@material-ui/core/Tooltip';

const Navbar = () => {
  const activeNavIcon = (path) => ({
    style: {
      stroke: window.location.pathname === path ? '#4c75f2' : '',
    },
  });

  return (
    <div className="Navbar">
      <div className="navbar-bottom">中文</div>

      <div className="navbar-top">
        <Link to="/">
          Covid19<span>中国</span>
        </Link>
      </div>

      <div className="navbar-middle">
        <>
          <Link to="/">
            <Tooltip title="首页">
              <span>
                <Home {...activeNavIcon('/')} />
              </span>
            </Tooltip>
          </Link>
          <Link to="/news">
            <Tooltip title="新闻">
              <span>
                <FileText {...activeNavIcon('/news')} />
              </span>
            </Tooltip>
          </Link>
          <Link to="/rumors">
            <Tooltip title="谣言">
              <span>
                <Shield {...activeNavIcon('/rumors')} />
              </span>
            </Tooltip>
          </Link>
          {/* TODO Add dark mode */}
          <span>
            <Sun />
          </span>
        </>
      </div>
    </div>
  );
};

export default Navbar;
